<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  import { slide } from "svelte/transition"
  import { sineIn, sineOut } from 'svelte/easing'
  import QuickEntry from "../../../_Common/QuickEntry/GenericItem.svelte"
  dispatch = createEventDispatcher()

  import Indent from "../Indent.svelte"


  export index = undefined
  export indentation = 0
  export item = { is_divider: true }
  export listId = undefined
  export firstItem = false


  isOver = false
  isHover = false
  isInvalid = false

  over = (e)->
    e.preventDefault()
    e.stopPropagation()
    isOver = true

    isInvalid = false

    # console.error "OVER"

    #if isInvalid
    #  return e.preventDefault()
    #console.log "return is invalid: #{!isInvalid}"
    # return !isInvalid

  hover = (e)->
    e.preventDefault()
    e.stopPropagation()
    unless isOver
      isHover = true
      # console.error "HOVER"

  unhover = (e)->
    e.preventDefault()
    e.stopPropagation()
    unless isOver
      # console.log "UNHOVER"
      isHover = false

  leave = (e)->
    draggable = false
    isOver = false
    isInvalid = false
    # console.error "LEAVE"
    # isHover = false

  add = (e)->

    toIndex = item.index
    if firstItem
      if index # takes priority
        toIndex = index
      else
        toIndex = 0

    isHover = false
    console.error "divider add"
    console.debug { ancestry: item.ancestry, index: toIndex }
    dispatch "add", { ancestry: item.ancestry, index: toIndex }


  drop = (e)->
    isOver = false
    isInvalid = false
    draggable = false


    eventType = "drop"


    #console.log "NestableDivider.svelte"

    e.preventDefault()
    dragItem = JSON.parse(e.dataTransfer.getData("text/plain"))

    #console.log item

    #console.log "--------------------------------"

    unless firstItem
      if item.list_items.length > 0
        console.log "------------------------------ DIVIDER DROP"
        console.log "dragitem"
        console.debug dragItem
        console.log "dropitem"
        console.log item


        console.log "item.list_items.length #{item.list_items.length} :: nest"
        eventType = "nest"
        item["to_index"] = 0
      else

        console.log "DROP ITEM #{item.name} index: #{item.index}"
        item["to_index"] = item.index + 1

      # console.log "evntType: #{eventType}"

      dropped = true

      unless Object.has(item, "is_divider")
        item["is_divider"] = true

      #console.log "DroppableDivider - drop"
      #console.log item

    else # handle first item
      console.error "FRIST ITEM"
      item["to_index"] = 0

      console.log dragItem
      console.log "eventType #{eventType}"
      dropped = true

    if dropped
      dispatch eventType,
        divider: true
        dragItem: dragItem
        dragIndex: dragItem.index
        dragListId: dragItem.listId
        dropItem: item
        dropIndex: index
        dropListId: listId


</script>

<div
  divider
  layout="row"
  class:over={isOver}
  class:hover={isHover}
  on:drop={drop}
  on:dragover={over}
  on:dragleave={leave}
    on:mouseenter={hover}
    on:mouseleave={unhover}
>
  <div spacer flex="initial"/>
  <Indent level={indentation} flex="initial"/>
  <div flex

    class:over={isOver}
    class:hover={isHover}
    class="indented"
  >
    {#if isHover}
      <div class="add-divider"
        in:slide="{{delay: 250, duration: 300, easing: sineIn }}"
        class:invalid={isInvalid}
         on:click={add}
         layout="row"
      >
          <div flex="initial" icon><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M12,20C7.59,20 4,16.41 4,12C4,7.59 7.59,4 12,4C16.41,4 20,7.59 20,12C20,16.41 16.41,20 12,20M12,2A10,10 0 0,0 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2M13,7H11V11H7V13H11V17H13V13H17V11H13V7Z"/></svg></div>
          <div flex>Click to Add New List Item Here</div>
      </div>
    {/if}
      <div class="droptarget"
        class:invalid={isInvalid}>
        Drop to Move List Item Here
      </div>


  </div>
</div>


