  div[divider]
    // margin: 2px 0px
    height: 8px
    transition: height 0.15s ease-out
    border-radius: 4px

    .indented
      border-radius: 4px
      background: #f1f1f1

    &:hover, &.over
      height: 24px
      transition: height 0.25s ease-in

    &:hover
      //padding: 8px 0px
      border-top: 8px solid transparent
      border-bottom: 8px solid transparent

      height: 40px

      .indented
        background: transparent
    //background: var(--lightest-blue)


    // drag and drop
    .droptarget
      height: 100%
      color: transparent
      line-height: 24px

    .indented.over
      .droptarget
        padding: 0px 8px
        color: white !important
        background: var(--green)
        border-radius: 4px

    .indented.hover



    .add-divider
      text-align: left
      border-radius: 3px
      line-height: 24px
      vertical-align: middle
      [icon]
        padding-right: 8px
        svg
          fill: var(--green)
        color: var(--green)
      // background: var(--green) !important
    // hover state






    &.invalid
      .placeholder
        background: var(--red) !important




    div[spacer] // accommodate expand collapse icon
      width: 24px

</style>
